<template>
  <div class="layout-box">
    <div class="author-box">
      <author></author>
    </div>
    <div class="view-container">
      <router-view></router-view>
    </div>
    <particles></particles>
  </div>
</template>

<script>
const particles = resolve => require(['@/components/vueParticles/index'], resolve)
const author = resolve => require(['@/components/author/author.vue'], resolve)
export default {
  name: 'layout',
  components: {
    particles,
    author
  }
}
</script>

<style scoped lang="scss">
  .layout-box {
    height: 100%;
    margin-top: 50px;
    padding-left: 20px;
  }
  .author-box {
    float: left;
    width: 260px;
  }
  .view-container {
    position: relative;
    overflow: auto;
    height: 90vh;
    margin-left: 260px;
    padding: 0 10px;
  }
</style>



