import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/view/layout'
import test from '@/view/showArticle'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      component: layout,
      children: [
        {
          path: '/test',
          name: 'test',
          component: test
        }
      ]
    }
  ]
})



